<template>
  <div class="article-page">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card shadow="never" border="false">
      <template #header>
        <div class="header">
          <span>{{ total }}</span>
          <el-button
            icon="el-icon-plus"
            size="small"
            type="primary"
            round
            @click="open('add')"
          >
            
          </el-button>
        </div>
      </template>
      <!--  -->
      <!-- data  -->
      <!-- el-table-column -->
      <!-- el-table-column label   -->
      <!-- el-table-column prop  -->
      <el-table :data="list" style="width: 100%">
        <el-table-column prop="stem" label="" width="400">
        </el-table-column>
        <el-table-column prop="creator" label="" />
        <el-table-column prop="likeCount" label="" />
        <el-table-column prop="views" label="" />
        <el-table-column prop="createdAt" label="" width="200" />
        <el-table-column label="" width="120">
          <!--    #default  -->
          <!-- 
               "default"  -->
          <template #default="{ row }">
            <div class="actions">
              <!--  -->
              <i class="el-icon-view" @click="open('preview', row)"></i>
              <!--  -->
              <i class="el-icon-edit-outline" @click="open('edit', row)"></i>
              <i class="el-icon-delete" @click="Del(row.id)"></i>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        background
        @current-change="handleCurrentChange"
        :current-page="current"
        layout="total,sizes,prev, pager, next, jumper"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="10"
        :total="total"
      >
      </el-pagination>
      <!--  -->
    </el-card>
    <!-- rtlltrttbbtt -->
    <el-drawer
      :title="menu[type]"
      :visible.sync="isShow"
      direction="rtl"
      size="60%"
      @closed="resetForm"
    >
      <!-- <span>!</span> -->
      <div v-if="type === 'preview'" class="article-preview">
        <h5>{{ form.stem }}</h5>
        <div v-html="form.content"></div>
      </div>
      <el-form
        v-else
        :model="form"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="stem">
          <el-input v-model="form.stem"></el-input>
        </el-form-item>

        <el-form-item label="" prop="content">
          <!--  -->
          <quill-editor
            v-model="form.content"
            @blur="$refs.ruleForm.validateField('content')"
          ></quill-editor>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSumbit"></el-button>
          <el-button @click="resetForm"></el-button>
        </el-form-item>
      </el-form>
    </el-drawer>
  </div>
</template>

<script>
import { getArticleList, addArticle, updateArticleDetail, delArticle } from '@/api/article'
// require styles
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

import { quillEditor } from 'vue-quill-editor'
export default {
  name: 'article-page',
  components: {
    quillEditor
  },
  data () {
    return {
      current: 1, // 
      pageSize: 10, // 
      total: 0, // 
      list: [], // 
      // pageTotal: 0, // 40
      isShow: false,
      menu: {
        add: '',
        edit: '',
        preview: ''
      },
      type: '', // 
      // 
      form: {
        stem: '',
        content: ''
      },
      rules: {
        stem: [{ required: true, message: '', trigger: 'blur' }],
        content: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },

  created () {
    this.initData()
  },
  methods: {

    // 
    async initData () {
      const { data } = await getArticleList({
        current: this.current,
        pageSize: this.pageSize
      })
      this.list = data.rows // list
      this.total = data.total // 400
      // this.pageTotal = data.pageTotal // 40
      // console.log(data)
    },
    // 
    open (val, data) {
      // console.log(this.type)
      this.type = val
      // 
      this.isShow = true
      if (val === 'edit') {
        // this.form = data
        this.form = { ...data }
      }
      if (val === 'preview') {
        // this.form = data
        this.form = { ...data }
      }
    },
    // 
    resetForm () {
      if (this.type !== 'preview') {
        this.$refs.ruleForm.resetFields()
      }
      // this.$refs.ruleForm && this.$refs.ruleForm.resetFields()
      this.form = {
        stem: '',
        content: ''
      }
    },
    // ---------
    async onSumbit () {
      if (this.type === 'add') {
        // console.log('ok')

        try {
          //  addArticle(this.form) 
          await addArticle(this.form)
          // 
          this.$message.success('')
          // 
          this.initData()
          // 
          this.isShow = false
          // 
          this.resetForm()
        } catch (error) {
          // 
          if (error.response) this.$message.error(error.response.data.message)
        }
      } else if (this.type === 'edit') {
        // console.log('ok')
        try {
          // 
          await updateArticleDetail({
            id: this.form.id,
            stem: this.form.stem,
            content: this.form.content
          })
          // 
          this.$message.success('')
          // 
          this.initData()
          // 
          this.isShow = false
          // 
          this.resetForm()
        } catch (error) {
          // 
          if (error.response) this.$message.error(error.response.data.message)
        }
      }
    },
    // -----
    handleSizeChange (val) {
      // console.log(` ${val} `)
      this.pageSize = val
      this.initData()
    },
    handleCurrentChange (val) {
      // console.log(`: ${val}`)
      this.current = val
      // 
      this.initData()
    },
    // 
    Del (id) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        // 
        //  
        try {
          await delArticle(id)
          this.$message.success('')
          this.initData()
        } catch (error) {
          if (error.response) this.$message.error(error.response.data.message)
        }
      }).catch(() => {
        this.$message({
          // 
          type: 'info',
          message: ''
        })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.el-card {
  margin-top: 25px;
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 16px;
  }
  .actions {
    font-size: 18px;
    display: flex;
    justify-content: space-around;
    color: #666;
    > i:hover {
      color: rgba(114, 124, 245, 1);
      cursor: pointer;
    }
  }
}
.el-pagination {
  margin-top: 20px;
  text-align: center;
}
.el-breadcrumb {
  margin-top: 10px;
}
.el-form {
  padding-right: 40px;
}
.quill-editor {
  ::v-deep .ql-editor {
    height: 300px;
  }
}
.el-rate {
  padding: 10px 0;
}
.article-preview {
  padding: 0 40px 40px 40px;
  > h5 {
    font-size: 20px;
    color: #666;
    border-bottom: 1px dashed #ccc;
    padding-bottom: 30px;
    margin: 0 0 20px 0;
  }
}
</style>
